// 
// import toQueue from './js/add-to-watched'

//    '''toQueue.setQueueOne(obj)'''         .

//     setQueueOne()    (   )       (LS),
//    true     false.

//        toQueue.getQueueOne()   ,   LS   id ,     ,
//      ,      Json  LS   undefined,     LS  false.

//       toQueue.getQueueAll()   
//      ,      Json  LS   undefined,     LS  false.

//     removeQueueOneEl()    ,    LS      true    ,
//     Json  LS   undefined,     LS  false.

const QUEUE = 'to-queue';

export const toQueue = {
  addMovie(movie, arr) {
    if (arr.every(e => e.id !== movie.id)) {
      arr.unshift(movie);
    }
    return arr;
  },

  removeMovie(id, arr) {
    return arr.filter(e => e.id !== id);
  },

  getQueueAll() {
    try {
      if (localStorage.getItem(QUEUE)) {
        return JSON.parse(localStorage.getItem(QUEUE));
      }
      return undefined;
    } catch {
      return false;
    }
  },

  getQueueOne(id) {
    try {
      if (localStorage.getItem(QUEUE)) {
        const arr = JSON.parse(localStorage.getItem(QUEUE));

        return arr.filter(e => e.id === id);
      }
      return undefined;
    } catch {
      return false;
    }
  },

  setQueueOne(el) {
    try {
      if (localStorage.getItem(QUEUE)) {
        const arr = JSON.parse(localStorage.getItem(QUEUE));

        localStorage.setItem(QUEUE, JSON.stringify(this.addMovie(el, arr)));
        return true;
      }

      const arrMovie = [];

      arrMovie.push(el);
      localStorage.setItem(QUEUE, JSON.stringify(arrMovie));

      return true;
    } catch {
      return false;
    }
  },

  removeQueueOneEl(id) {
    try {
      if (localStorage.getItem(QUEUE)) {
        const arr = JSON.parse(localStorage.getItem(QUEUE));

        localStorage.setItem(QUEUE, JSON.stringify(this.removeMovie(id, arr)));
        return true;
      }
      return undefined;
    } catch {
      return false;
    }
  },
};
